<template>
	<div class="content">
		<div class="row">
			<span class="ued-tag">基础用法</span><input type="text" class="ued-input" placeholder="请输入内容"><a href="javascript:void(0)" class="view" @click="view('ued-input')">示例代码</a>
		</div>
		<div class="row">
			<span class="ued-tag">复合型输入框</span>
			<div class="ued-input-compose" ref="ued-input-compose">
				<div class="ued-input-compose-tag">请选择<i></i></div>
				<input type="text" class="ued-input" placeholder="请输入内容">
			</div>
			<a href="javascript:void(0)" class="view" @click="view('ued-input-compose')">示例代码</a>
			<div class="ued-input-compose ued-input-compose_bg">
				<div class="ued-input-compose-tag">Http://</div>
				<input type="text" class="ued-input" placeholder="请输入内容">
			</div>
			<a href="javascript:void(0)" class="view">示例代码</a>
		</div>
	</div>
</template>

<script>
import eking from "@/lib/eking"
import sweet from "sweetalert"
// import hljs from "hljs"
// console.log(hljs)
export default{
	name: 'basicColor',
	data(){
		return {
			preHtml : ''
		};
	},
	methods:{
		view(param){
			var _this = this;
			var map = {
				'ued-input':function(){
                    sweet({
                        content: {
                            element: "code",
                            attributes: {
                                innerHTML: '',
                                width: 500
                            },
                        }
                    })
				},
				'ued-input-compose':function(){
                    sweet({
                        content: {
                            element: "code",
                            attributes: {
                                innerHTML: '',
                                width: 500
                            },
                        }
                    })
				}
			}

			map[param] && map[param]();
		}
	},
	mounted(){
		this.$emit("hideHeaderAndLeft",{show:true});
		// hljs.initHighlightingOnLoad();
	}
}	
</script>

<style>

</style>


